.NavBar ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
  background-color: #333333;
}

.NavBar li {
  float: left;
}

.NavBar li a {
  display: block;
  color: white;
  text-align: center;
  padding: 16px;
  text-decoration: none;
}

.NavBar li a:hover {
  background-color: #111111;
}

.NavLink{
    text-decoration: None;
    font-size: 200%;
    background-color: red;
    color: green;
}
